.Questions {
  width: 400px;
  max-width: 100%;
  margin: 0 auto;
}

.Questions__question {
  background-color: white;
  box-shadow: 0 0 5px black;
  margin: 20px;
  padding: 20px;
  cursor: pointer;

  &:hover {
    box-shadow: 0 0 10px black;
  }

  .fa-check {
    @include perfect-center;
    position: absolute;
    right: 10px;
    top: 0;
    bottom: 0;
    font-size: 1.5em;
    color: rgb(2, 63, 2);
  }
}

.question__text {
  font-size: 1.3em;
  margin-bottom: 20px;
}

.question__variants {
  display: flex;
  flex-direction: column;
}

.question__variant {
  @include border(1px, black);
  padding: 10px;
  margin: 2px 0;
  position: relative;
}

.question__variant-correct {
  // background-color: rgb(24, 185, 24);
  @include border(1px, rgb(0, 255, 0));
  // color: white;
}

.question__variant-incorrect {
  // background-color: rgb(223, 35, 35);
  @include border(1px, red);
  // color: white;
}
