.Alert {
  @include perfect-center;
  flex-direction: column;
  background-color: white;
  border: 3px solid rgb(255, 0, 0);
  position: fixed;
  width: 300px;
  height: 150px;
  top: -150px;
  left: 50%;
  margin-left: -150px;
  border-radius: 10px;
  animation: {
    duration: .3s;
    timing-function: linear;
    fill-mode: forwards;
  }

  p {
    color: red;
    font-size: 20px;
  }
}

.Alert__close-btn {
  border: none;
  background-color: white;
  border: 2px solid green;
  color: green;
  width: 80px;
  height: 30px;
  margin-top: 40px;
  font-weight: bold;
  cursor: pointer;

  &:hover {
    background-color: green;
    color: white;
  }
}

.anim-alert-down {
  animation-name: alert-down;
}

.anim-alert-up {
  animation-name: alert-up;
}

@keyframes alert-down {
  from {
    top: -150px;
  }
  to {
    top: 100px;
  }
}

@keyframes alert-up {
  from {
    top: 100px;
  }
  to {
    top: -150px;
  }
}
