$navtoggle-width: 200px;

.NavToggle {
  @include border(3px);
  background-color: white;
  display: flex;
  width: $navtoggle-width;
  flex-direction: column;
  align-items: center;
  position: fixed;
  top: 0;
  right: -200px;
  border-top-left-radius: 15px;
  border-bottom-left-radius: 15px;
  animation: {
    duration: .3s;
    fill-mode: forwards;
    timing-function: linear;
  }
}

.animate-open {
  animation-name: open;
}

.animate-close {
  animation-name: close;
}

.NavToggle__link {
  @include perfect-center;
  text-align: center;
  width: 100%;
  height: 60px;
  border-top-left-radius: 15px;
  border-bottom-left-radius: 15px;

  &:hover {
    background-color: grey;
  }
}

.fas.fa-times {
  position: absolute;
  top: 10px;
  left: 10px;
  font-size: 1.5em;
  cursor: pointer;

  &:hover {
    color: grey;
  }
}

@keyframes open {
  from {
    right: -200px;
  }
  to {
    right: 0;
  }
}

@keyframes close {
  from {
    right: 0;
  }
  to {
    right: -200px;
  }
}
