.Play {
  
}

.Play__question {
  @include border;
  @include perfect-center;
  background-color: white;
  text-align: left;
  width: 400px;
  max-width: 100%;
  min-height: 300px;
  margin: 0 auto;
  padding: 10px;
  position: relative;
}

.Play__question__text {
  font-size: 1.3em;
  text-align: center;
}

.Play__question__variants {
  display: flex;
  flex-wrap: wrap;
  position: absolute;
  bottom: 10px;
  left: 10px;
}

.Play__question__variant {
  @include border(1px, rgb(60, 146, 175));
  margin: 0 10px;
  cursor: pointer;
  padding: 5px 10px;
}

.Play__results {
  @include border;
  @include perfect-center;
  text-align: center;
  background-color: white;
  width: 300px;
  margin: 0 auto;
  position: relative;
  height: 300px;
}

.results__play-again {
  border: none;
  background-color: rgb(70, 185, 70);
  width: 100px;
  height: 30px;
  position: absolute;
  right: 10px;
  bottom: 10px;
  cursor: pointer;
  color: white;
}
