.Header {
  @include border;
  height: 60px;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  background-color: rgb(31, 180, 56);
}

.Header__logo {
  font-size: 2em;
  margin: 0 50px;
  font-weight: bold;
  text-shadow: 0 0 3px black;
}

.nav {
  display: flex;
}

.nav__link {
  margin: 0 20px;
  font-size: 20px;
  color: white !important;
}

.fas.fa-bars {
  display: none;
  font-size: 2em;
  margin-right: 30px;
  cursor: pointer;

  &:hover {
    color: grey;
  }
}

@media (max-width: 686px) {
  .nav {
    display: none;
  }

  .Header {
    justify-content: space-between;
  }

  .fas.fas.fa-bars {
    display: initial;
  }
}
