* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

body {
  font-family: sans-serif;
  background-color: rgb(179, 178, 178);
}

ul {
  list-style-type: none;
}

input,
button {
  outline: none;
}

h1 {
  text-align: center;
  margin: 30px 0;
}

a, a:link, a:active, a:visited {
  color: black;
  text-decoration: none;
}

input, textarea {
  border: none;
  outline: none;
}
