.Add {
  text-align: center;
}

.Add__form {
  width: 290px;
  margin: 0 auto;
  background-color: white;
  padding: 20px;
  box-shadow: 0 0 5px black;
}

textarea {
  @include border;
  background-color: transparent;
  border: none;
  border-bottom: 1px solid rgb(25, 0, 255);
  margin-bottom: 10px;
  resize: vertical;
  height: 100px;
  font-size: 1.5em;
  padding: 10px;
  width: 100%;
}

.form__variant-wrapper {
  width: 100%;
  height: 40px;
  display: flex;
}

.form__variant-input {
  flex-grow: 1;
  text-indent: 10px;
  background-color: transparent;
  border-bottom: 1px solid rgb(0, 89, 100);
}

.form__variant-tick {
  @include perfect-center;
  height: 100%;
  flex-basis: 40px;

  .fa-check {
    font-size: 30px;
    color: rgb(0, 107, 0);
  }
}

.form__submit {
  width: 100%;
  margin-top: 30px;
  height: 60px;
  font-weight: bold;
  color: white;
  font-size: 26px;
  cursor: pointer;
  background-image: linear-gradient(to right, #cc2b5e, #753a88);
}
